.main{
  /*background-color: #bcb7b7;*/
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}
.hide-label {
    width: 0px;
    font-size: 1px;
    position: absolute;
}
.App-header {
  background-color: #222;
  /*height: 100px;*/
  margin-bottom: 0px;
  color: white;
  width:100%;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.App-title {
  color:#FFC107;
  font-size: 1.5em;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif; 
  width: 80%;
}
.header__menu {
  display: inline-block;
  /*position: absolute;*/
  right: 0;
  /*padding: 1em;*/
  width: 20%;
}
a#menu svg {
  width: 40px;
  fill: #00bcd4fa;
}
.nav{
    z-index: 10;
    background-color: #fff;
    width: 400px;
        /*height: 100%;*/
        position: absolute;
        /* This trasform moves the drawer off canvas. */
        -webkit-transform: translate(-400px, 0);
        -ms-transform: translate(-400px, 0);
            transform: translate(-400px, 0);
        /* Optionally, we animate the drawer. */
        -webkit-transition: -webkit-transform 2s ease;
        transition: -webkit-transform 2s ease;
        -o-transition: transform 2s ease;
        transition: transform 2s ease;
        transition: transform 2s ease, -webkit-transform 2s ease;
  }
.nav.open{
  -webkit-transform: translate(0, 0);
      -ms-transform: translate(0, 0);
      transform: translate(0, 0);
}
/*----------------------------------------------------------------------*/
.App-intro {
  font-size: large;
}

#main-container {
  /*display: flex;*/
  width: 100%;
  height: 100%;
}
#map-container {
  width: 100%;
  height: 100%;
  display: block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;

}

ul.locations-list {
  display: block;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  height: 530px;
  color: #f3f0f0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.7;
  background-color: #2E3D49;
  margin-bottom: 3px;
  overflow: scroll;
  /*overflow: hidden;*/
  /*text-overflow: ellipsis;*/
}
ul.selectedcolor {
  color: yellow;
  display: block;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  height: 82vh;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.7;
  background-color: #2E3D49;
  margin-bottom: 3px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}
.filter-container{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  width: 100%;
}
#search-filter-text {
  width :70%;
  height: 30px;
}
.search-filter {
  width: 30%;
  height: 36px; /* top left, top right, bottom right, bottom left */
  border-radius: 0px 5px 5px 0px;
  background-color: #02b3e4;
  color: white;
  font-size: 29px;
}
.App-footer {
  height: 100%;
  width: 100%;
  display: block;
}
.App-footer-text{
  font-size: 12px;
  font-family: Arial, Helvetica, sans-serif;
  font-style: italic;
  text-align: center;
  background-color: #222;
  color: white;
  line-height: 3em;
}
.picture-Style {
  width: 270px;
  height: 270px;
}
.site-image {
  width: 220px;
  height: 220px;
}
ul {
  list-style-type: none;
  margin: 0;
  padding-left: 3px;
}
@media screen and (max-width: 399px) {
  .nav{
    width: 350px;
    -webkit-transform: translate(-350px, 0);
    -ms-transform: translate(-350px, 0);
        transform: translate(-350px, 0);
  }
   .App-title {
    font-size: 1em;
  }
}
@media screen and  (max-width: 367px) {
  .nav{
    width: 320px;
    -webkit-transform: translate(-320px, 0);
    -ms-transform: translate(-320px, 0);
        transform: translate(-320px, 0);
  }
  .App-title {
    font-size: 1em;
  }
  .search-filter {
    font-size: 20px;
  }
}
@media screen and  (max-width: 337px) {
  .nav{
    width: 300px;
    -webkit-transform: translate(-300px, 0);
    -ms-transform: translate(-300px, 0);
        transform: translate(-300px, 0);
  }
}
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



